import React, { PropTypes } from 'react'
import { connect } from 'react-redux'
import { Select, Form, Input, Button, Icon, Table, Modal, Menu, Dropdown, Spin } from 'antd'
import moment from 'moment'

import { getEndpoints, getCounters, getCharts, saveSearchText, getId } from '../modules/index'
import { registerModules } from 'src/modules'

import './SearchData.scss'

const FormItem = Form.Item
const Option = Select.Option


class SearchData extends React.PureComponent {
  state = {
    endpointInputBorderColor: '#d9d9d9',
    endpoint: '',
    tag: '',
    counter: '',
    searchEndpointsText: '',
    searchCountersText: '',
    selectedEndpointRowKeys: [],
    selectedCounterRowKeys: [],
    selectedEndpointRows: [],
    selectedCounterRows: [],
    endpointsLimit: '50',
    countersLimit: '50',
  }
  componentDidMount() {
    this.props.registerModules({
      search: {
        enable: false,
      },
      sidebar: {
        enable: false,
      }
    })
    const { searchText } = this.props.dashboard.toJS()
    const { endpoint, tag, counter, selectedEndpointRows, selectedCounterRows } = searchText
    const selectedEndpointRowKeys = selectedEndpointRows.length === 0 ? [] : selectedEndpointRows.map(obj => obj.id)
    const selectedCounterRowKeys = selectedCounterRows.length === 0 ? [] : selectedCounterRows.map(obj => obj.key)
    this.setState({endpoint, tag, counter, selectedEndpointRows, selectedCounterRows, selectedEndpointRowKeys, selectedCounterRowKeys})
  }

  changeEndpoint = (e) => {
    const endpointText = e.target.value
    this.setState({endpoint: endpointText})
    const str = endpointText.replace(/\s+/g, '')
    // 除去所有空格后字符串长度为0，说明没有输入
    if (str.length === 0) {
      this.setState({endpointInputBorderColor: 'red'})
    } else {
      this.setState({endpointInputBorderColor: '#d9d9d9'})
    }
  }

  changeTag = (e) => {
    this.setState({tag: e.target.value})
  }

  changeCounter = (e) => {
    this.setState({counter: e.target.value})
  }

  onInputEndpointChange = (e) => {
    this.setState({ searchEndpointsText: e.target.value, selectedEndpointRowKeys: [], selectedEndpointRows: [] })
  }

  onInputCounterChange = (e) => {
    this.setState({ searchCountersText: e.target.value, selectedCounterRowKeys: [], selectedCounterRows: [] })
  }

  queryEndpoints = (e) => {
    const { endpoint, tag } = this.state
    if ((endpoint.replace(/[\s]/g, '') === '') &&
    (tag.replace(/[\s]/g, '') === '')) {
      Modal.warning({
        title: '提示',
        content: '请填写有效Endpoint或者标签！',
      })
      return false
    }
    // 去请求数据之前先把目前选中的endpoints重置
    this.setState({ selectedEndpointRowKeys: [], selectedEndpointRows: [] })
    if (e.type === 'submit') {
      e.preventDefault()
      this.props.getEndpoints('getEndpoints', endpoint, tag, this.state.endpointsLimit)
    } else {
      this.setState({endpointsLimit: e})
      this.props.getEndpoints('getEndpoints', endpoint, tag, e)
    }
  }

  queryCounters = (e) => {
    const selectedRows = this.state.selectedEndpointRows
    if (selectedRows.length === 0) {
      Modal.warning({
        title: '提示',
        content: '请勾选要查看的endpoints',
      })
      return false
    }
    // 去请求数据之前先把目前选中的counters重置
    this.setState({ selectedCounterRowKeys: [], selectedCounterRows: [] })
    const endpointIds = selectedRows.map(obj => obj.id).join()
    const { counter } = this.state
    // 先判断是全局搜索还是切换limit
    if (e.type === 'submit') {
      e.preventDefault()
      this.props.getCounters(endpointIds, counter, this.state.countersLimit)
    } else {
      this.setState({countersLimit: e})
      this.props.getCounters(endpointIds, counter, e)
    }
  }

  showCharts = (title, singleCounter) => {
    // 获取选中的endpoints name和conuters name,把他们转化成两个数组
    const selectedEndpointRows = this.state.selectedEndpointRows
    const selectedCounterRows = this.state.selectedCounterRows
    // 如果点击画图按钮，判断是否选择了endpoint和counter
    if (selectedEndpointRows.length === 0 || (singleCounter === undefined && selectedCounterRows.length === 0)) {
      Modal.warning({
        title: '提示',
        content: '请勾选要查看的endpoints和counters！',
      })
      return false
    }
    const endpoints = selectedEndpointRows.map(obj => obj.endpoint)
    const counters = (singleCounter === undefined) ?
    selectedCounterRows.map(obj => obj.counter) : [singleCounter]
    const endTime = moment().unix() // 单位是秒
    const startTime = moment().subtract(1, 'hour').unix()
    // 把如下数据存入store中以便在其他地方使用
    this.props.saveSearchText({
      endpoint: this.state.endpoint,
      tag: this.state.tag,
      counter: this.state.counter,
      selectedEndpointRows: this.state.selectedEndpointRows,
      selectedCounterRows: this.state.selectedCounterRows,
    })
    // singleCounter不为undefined时说明点击的查看大图
    this.props.getId(endpoints, counters, title, startTime, endTime, singleCounter)
  }

  render () {
    const { endpoints, counters, isLoading } = this.props.dashboard.toJS()
    const endpointsColumns = [{
      title: 'Endpoints',
      dataIndex: 'endpoint',
      key: 'id',
    }]
    const rowEndpointSelection = {
      selections: true,
      selectedRowKeys: this.state.selectedEndpointRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({selectedEndpointRowKeys: selectedRowKeys, selectedEndpointRows: selectedRows})
      },
      onSelectInvert: selectedRows => {
        let selectedEndpointRows = selectedRows
        if (selectedRows.length !== 0) {
          selectedEndpointRows = selectedRows.map(item => {
            return endpoints.filter(obj => obj.id === item)[0]
          })
        }
        this.setState({ selectedEndpointRows, selectedEndpointRowKeys: selectedRows })
      },
    }
    const rowCounterSelection = {
      selections: true,
      selectedRowKeys: this.state.selectedCounterRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({selectedCounterRowKeys: selectedRowKeys, selectedCounterRows: selectedRows})
      },
      onSelectInvert: selectedRows => {
        let selectedCounterRows = selectedRows
        if (selectedRows.length !== 0) {
          selectedCounterRows = selectedRows.map(item => {
            return counters.filter(obj => obj.key === item)[0]
          })
        }
        this.setState({ selectedCounterRows, selectedCounterRowKeys: selectedRows })
      },
    }
    let count = 0
    const countersKeyData = counters.map(obj => {
      const newObj = obj
      count += 1
      newObj.key = count
      return newObj
    })
    const countersColumns = [{
      title: 'Counters',
      dataIndex: 'counter',
      key: 'key',
      render: (text, record) => <a onClick={() => this.showCharts('Endpoint', record.counter)}>{text}</a>
    }, {
      title: '类型',
      dataIndex: 'type',
      width: 60,
    }, {
      title: '频率',
      dataIndex: 'step',
      width: 50,
    }]
    const menu = (
      <Menu>
        <Menu.Item>
          <Button type="dashed" onClick={() => this.showCharts('Endpoint')}>Endpoint视角</Button>
        </Menu.Item>
        <Menu.Item>
          <Button type="dashed" onClick={() => this.showCharts('Counter')}>Counter视角</Button>
        </Menu.Item>
        <Menu.Item>
          <Button type="dashed" onClick={() => this.showCharts('组合')}>组合视角</Button>
        </Menu.Item>
      </Menu>
    )
    const formEndpointItemLayout = {
      labelCol: { span: 4 },
      wrapperCol: { span: 19 },
    }
    const formCounterItemLayout = {
      labelCol: { span: 2 },
      wrapperCol: { span: 21 },
    }
    const tailEndpointFormItemLayout = {
      wrapperCol: {
        sm: {
          span: 19,
          offset: 4,
        },
      },
    }
    const tailCounterFormItemLayout = {
      wrapperCol: {
        sm: {
          span: 21,
          offset: 2,
        },
      },
    }
    return (
      <div id="dashboard-container">
        <div className="endpoints">
          <Form onSubmit={this.queryEndpoints}>
            <div className="content">
              <div className="header">
                <span className="num">1</span>
                <span className="title">搜索Endpoints</span>
              </div>
              <div className="up">
                <div>
                  <FormItem {...formEndpointItemLayout} label="Endpoint">
                    <Input
                      id="txtDashBoardEndPoint"
                      placeholder="可以用空格分割多个搜索关键字"
                      onChange={this.changeEndpoint}
                      value={this.state.endpoint}
                      style={{borderColor: this.state.endpointInputBorderColor}}/>
                  </FormItem>
                  <FormItem {...formEndpointItemLayout} label="标签">
                    <Input
                      id="txtDashBoardTag"
                      placeholder="eg:job=appstore-web"
                      value={this.state.tag}
                      onChange={this.changeTag}
                    />
                  </FormItem>
                  <FormItem {...tailEndpointFormItemLayout}>
                    <Button type="primary" htmlType="submit">全局搜索</Button>
                  </FormItem>
                </div>
              </div>

              {endpoints.length === 0 ?
                (<div className="down">
                  无数据
                </div>) :
                (<div>
                  <div id="table-header">
                    <FormItem>
                      <Select value={this.state.endpointsLimit} style={{ width: 80, position: 'relative', top: -1 }} onChange={this.queryEndpoints}>
                        <Option value="50">Limit50</Option>
                        <Option value="100">Limit100</Option>
                        <Option value="500">Limit500</Option>
                      </Select>
                    </FormItem>
                    <FormItem className="check">
                      <Input
                        style={{ width: 200 }}
                        placeholder="请输入过滤信息"
                        suffix={<Icon type="filter" />}
                        value={this.state.searchEndpointsText}
                        onChange={this.onInputEndpointChange}
                      />
                    </FormItem>
                  </div>
                  <div id="endpoints-results">
                    <Table
                      className="narrow-rows"
                      rowKey={record => record.id}
                      rowSelection={rowEndpointSelection}
                      columns={endpointsColumns}
                      dataSource={
                        endpoints.filter(item => {
                          const str = this.state.searchEndpointsText
                          try{
                            const reg = new RegExp(str, 'gi')
                            return item.endpoint.match(reg)
                          } catch(e) {
                            return true
                          }
                        })
                      }
                      pagination={false}
                    />
                  </div>
                </div>
                )
              }
            </div>
          </Form>
        </div>
        {isLoading === 0 ? <div className="dashboard-searchdata-loading"><Spin size="large" /></div> : ''}
        <div className="counters">
          <p className="header">
            <span className="num">2</span>
            <span className="title">搜索Counters</span>
          </p>
          <div className="up">
            <Form onSubmit={this.queryCounters}>
              <div className="content">
                <div>
                  <FormItem {...formCounterItemLayout} label="Counter">
                    <Input
                      id="txtDashBoardCounter"
                      placeholder="可以用空格分割多个搜索关键字"
                      value={this.state.counter}
                      onChange={this.changeCounter}
                    />
                  </FormItem>
                  <FormItem {...tailCounterFormItemLayout}>
                    <Button type="primary" htmlType="submit">搜索</Button>
                  </FormItem>
                </div>
              </div>
            </Form>
          </div>
          {counters.length === 0 ?
            (<div className="down">
              无数据
            </div>) :
            (<div id="counters-results">
              <Form>
                <div className="title">
                  <FormItem>
                    <Select value={this.state.countersLimit} style={{ width: 80 }} onChange={this.queryCounters}>
                      <Option value="50">Limit50</Option>
                      <Option value="100">Limit100</Option>
                      <Option value="500">Limit500</Option>
                    </Select>
                  </FormItem>
                  <FormItem>
                    <Input
                      style={{ width: 140 }}
                      placeholder="请输入过滤信息"
                      suffix={<Icon type="filter" />}
                      value={this.state.searchCountersText}
                      onChange={this.onInputCounterChange}
                    />
                  </FormItem>
                  <FormItem className="check">
                    <Dropdown overlay={menu}>
                      <Button type="default">看图</Button>
                    </Dropdown>
                  </FormItem>
                </div>
                <Table
                  className="narrow-rows"
                  rowSelection={rowCounterSelection}
                  columns={countersColumns}
                  dataSource={countersKeyData.filter(item => {
                    const str = this.state.searchCountersText.replace(/([.?*+^$[\]\\(){}|-])/g, "\\$1")
                    const reg = new RegExp(str, 'gi')
                    return item.counter.match(reg)
                  })}
                  pagination={false}
                />
              </Form>
            </div>)
          }
        </div>
      </div>
    )
  }
}

const mapActionCreators = {
  getEndpoints,
  getCounters,
  getCharts,
  saveSearchText,
  getId,
  registerModules,
}

const mapStateToProps = state => ({
  dashboard: state.dashboard,
})

export default connect(mapStateToProps, mapActionCreators)(SearchData)



